    .el-form
        .el-form-item
          background: #FBFBFC;
          border: 1px solid #EAECEF;
          margin: 0.2rem 0;
          padding-left: 0.2rem;
          position: relative;
          height: 0.5rem;
          // overflow: hidden;
    .btn
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        display: flex;
        justify-content: center;
        .submit, .cancle
            background: #20C6BA;
            border-radius: .04rem;
            color: #fff;
            font-size: .14rem;
            width: 1.2rem;
            height: .44rem;
