            font-size: .24rem;
            font-family: PingFangSC-Semibold;
            color: #263238;
        img
            width: .16rem;
            margin-left: .12rem;
            margin-right: .2rem;
    .editUserInfo
      padding-bottom: 0.5rem!important;
      .inputInfo
        .editTop
          width: 100%;
          height: 1.2rem;
          display: flex;
          align-items: center;
          justify-content: center;
          .headerImg
            width: 0.6rem;
            height: 0.6rem;
            border-radius: 50%;
            cursor: pointer;
            position: absolute;
        .editBottom
          width: 100%;
          .el-form
            display: flex;
            justify-content: center;
            .left,.center,.right
              display: flex;
              flex-direction: column;
              align-items: center;
            .center
              margin-left: 1.6rem;  
              margin-right: 1.6rem;
            .el-form-item
              margin-bottom: 0.12rem;
            .formNew
              width: 3.8rem;
              height: 0.5rem;
              display: flex;
              align-items: center;
              padding: 0 0.2rem;
              box-sizing: border-box;
              background: #FBFBFC;
              border: 1px solid #EAECEF;
              border-radius: 2px;
            .noback.formNew
              background: #fff;
            formNew.formArea 
              align-items: normal;        
    .userInfo
      width: 100%;
      padding-left: 0.14rem;
      padding-bottom: 0.2rem;
      background: #fff;
      box-sizing: border-box;
      .userId
        width: 100%;
        height: 0.6rem;
        line-height: 0.6rem;
        padding-left: 0.2rem;
        box-sizing: border-box;
        font-family: BebasNeueCyrillic;
        font-size: .2rem;
        color: #C0C4CC;
        border-bottom: 1px solid #EAECEF;
        margin: 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .operation
          display: flex;
          justify-content: flex-end;
          align-items: center;
          .cancle,.sub
            width: 1rem;
            height: 0.36rem;
            line-height: 0.36rem;
            text-align: center;
            border-radius: 0.04rem;
            cursor: pointer;
            font-size: 0.16rem;
          .cancle
            border: 1px solid #E9ECEF;
            color: #666;
          .sub
            background-image: linear-gradient(-138deg, #00DEAF 0%, #00DDB0 19%, #00B1BF 100%);
            color: #fff;
            margin-left: 0.12rem;
            margin-right: 0.36rem;
          .edit
            cursor: pointer;
            display: flex;
            width: 1rem;
            height: 0.36rem;
            justify-content: center;
            align-items: center;
            border: 1px solid #E9ECEF;
            border-radius: 0.04rem;
            color: #20C6BA;
            font-weight: 800;
            font-size: 0.16rem;
            margin-right: 0.2rem;
            .icon-btn_edit
              margin-right: 0.1rem;
    .infoDetail
      width: 100%;
      height: 1.69rem;
      .userHeadImg
        display: flex;
        height: 100%;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        float: left;
        margin-left: 1rem;
        margin-right: 1rem;
        .headImg
          width: 0.6rem;
          height: 0.6rem;
          border-radius: 50%;
          margin-bottom: 0.16rem;
        .username
          color: #263238;
          font-size: .16rem;
          .icon-icon_boy,
            color: #20C6BA;
            margin-left: .1rem;
          .icon-icon_girl
            color: #fc63b7;
            margin-left: .1rem;
      .infoList
        display: flex;
        flex-direction: column;
        float: left;
        .noflex.infoItem
          align-items: normal;
          .itemName
            width: 0.6rem;
        .infoItem
          display: flex;
          align-items: center;
          margin-bottom: 0.2rem;
          width: 3.2rem;
          .itemName
            width: 1rem;
            font-family: PingFangSC-Regular;
            font-size: 0.14rem;
            color: #8597A1;
          .itemValue
            flex: 1;
            font-family: PingFangSC-Semibold;
            font-size: 0.14rem;
            color: #263238;
          .itemValueList
            flex: 1;
            align-items: center;
            span
              margin-right: 0.1rem;
              font-size: 0.14rem;
              color: #263238;        
</style>
<style>
.sneakGuestEdit .editBottom .el-form-item__label{
  width: 1rem;
  white-space: nowrap;
  text-align: left;
}
.sneakGuestEdit .editBottom .formNew.formArea .el-form-item__label{
  width: 0.6rem;
  white-space: nowrap;
  text-align: left;
}
.sneakGuestEdit .yincan{
  visibility: hidden;
}
.sneakGuestEdit #fileToUpload{
    opacity: 0.01;
    width: 1rem;
    height: 100%;
    z-index: 12;
    outline: none;
    cursor: pointer;
}
.sneakGuestEdit .editBottom .el-form-item__content{
  flex: 1;
  line-height: 0.5rem; 
}
.sneakGuestEdit .birthday .el-input__prefix{
  right: 0px;
}
.sneakGuestEdit  .birthday .el-input__inner{
  padding-right: 0.4rem!important;
}
.sneakGuestEdit  .birthday  .el-input__icon{
  position: absolute;
  right: 0.05rem;
  width: 100%;
  line-height: 0.5rem;
}
.birthday .el-icon-date:before{
  position: absolute;
  right: 0.05rem;
}
.sneakGuestEdit  .el-date-editor.el-input, .sneakGuestEdit  .el-date-editor.el-input__inner{
  width: 100%;
}
.sneakGuestEdit .editBottom .el-form-item__content .el-input__inner{
  border: none;
  /* height: 0.48rem; */
  /* line-height: 0.48rem; */
  background: #FBFBFC;
  text-align: right;
  padding: 0;
  font-size: 0.16rem;
  color: #263238;
  height: 0.3rem;
  padding-right: 0.1rem;
}
.sneakGuestEdit .editBottom .noback.formNew .el-input__inner{
  background: #fff;
}
.sneakGuestEdit .formNew  .el-form-item__error{
  right: 0;
  text-align: right;
  position: absolute;
  top: 100%;
}
.el-form-item.is-required:not(.is-no-asterisk)>.el-form-item__label:before{
  margin: 0;
}
.sneakGuestEdit .el-form-item__label:before{
  content: ''!important;
  margin: 0;
}
.sneakGuestEdit  .nomust .el-form-item__label:after{
  content: '';
}
.sneakGuestEdit .el-form-item__label:after{
  content: '*';
  color: #f56c6c;
  margin-right: 4px;
  position: relative;
  top: 2px;
  left: 0px;
}
.sneakGuestEdit .formNew.errorTips{
  position: relative;
}
.sneakGuestEdit .formNew.errorTips .error{
  position: absolute;
  bottom: -0.4rem;
  color: #f56c6c;
  right: 0rem;
}
.sneakGuestEdit .formNew.formArea .el-form-item__label{
  font-size: 14px;
  color: #8597A1!important;
  line-height: 0.4rem;
}
.sneakGuestEdit .formNew.formArea{
  height: 1.2rem!important;
}
.sneakGuestEdit .el-select{
  width: 100%;
}
.sneakGuestEdit .editBottom .noback .el-form-item__content .el-input__inner{
  padding: 0 0.4rem;
  padding-left: 0;
}
.sneakGuestEdit .el-textarea__inner{
  font-size: 0.16rem;
  color: #263238;
  text-align: left;
  line-height: 0.22rem;
  border: none;
  background: none;
  resize:none ;
  font-family: none;
  text-align: left;
}
.sneakGuestEdit .editUserInfo .inputInfo .editBottom .el-form .formNew.formArea{
  align-items: normal;
}
.sneakGuestEdit ::-webkit-scrollbar{
  width: 4px;  
  height: 16px;  
  background-color: #dcdfe6;
}
.sneakGuestEdit ::-webkit-track{
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);  
  border-radius: 4px;  
  background-color: #fff;  
}
.sneakGuestEdit ::-webkit-thumb{
  border-radius: 10px;  
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);  
  background-color: #fff;
}
</style>




